import React, { Component } from 'react';
import { connect } from 'react-redux';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class QuickView extends Component {
    render() {
        const { closeModal } = this.props;
        return (
            <div className="modal fade productQuickView show" style={{paddingRight: '16px', display: 'block'}}>
                <ToastContainer />
                <div className="modal-dialog modal-dialog-centered" role="document">
                    <div className="modal-content">
                        <button type="button" onClick={closeModal} className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true"><i className="fas fa-times"></i></span>
                        </button>
                        <div className="row align-items-center">
                            <div className="col-lg-6 col-md-6">
                                <div className="productQuickView-image">
                                    <img src={this.props.image} alt="image" /> 
                                </div>
                            </div>

                            <div className="col-lg-6 col-md-6">
                                <div className="product-content">
                                <div className="about-content">
                                            <h2><b>{data.call}</b> - {data.name}</h2>
                                            <div className="table-responsive">
                                                <table className="table table-striped">
                                                    <tbody>
                                                    <tr>
                                                        <td>Born</td>
                                                        <td>{data.dob}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Sire</td>
                                                        <td>{data.sire}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Dam</td>
                                                        <td>{data.dam}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Owner</td>
                                                        <td>{data.owner}</td>
                                                    </tr>
                                                    <tr>
                                                        <td>Lives With</td>
                                                        <td>{data.lives}</td>
                                                    </tr> 
                                                         
                                                    <tr>
                                                        <td>AKA</td>
                                                        <td>{data.aka}</td>
                                                    </tr>
                                                    </tbody>
                                                </table>
                                                {this.state.display && data.Titles.length > 0 ? (
                                                    <div className="table-responsive">
                                                       <table className="table table-striped">
                                                        <thead>
                                                            <tr>
                                                                <th>Title</th><th>Date</th><th>Organization</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            {data.Titles.map((data, idx) => (
                                                                <tr>
                                                                    <td>{data.title}</td>
                                                                    <td>{data.date}</td>
                                                                    <td>{data.org}</td> 
                                                                </tr>
                                                            ))} 
                                                        </tbody>
                                                        </table>
                                                    </div>
                                                ) : ""}
                                            </div>
                                            </div>



                                    <a href="#" className="view-full-info">View full info</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


export default connect(
    null,
)(QuickView)
